export const formatWorkSheetData = (referenceNumber, country, declarantName, declarantTIN, regimeType, importerName, importerTIN, exporterName, exporterTIN, vesselName, portOfDischarger, deposit, containerCharges, additionalCharges, localFee, freightAmount, insurance, otherCharges, freightCurrency, freightRateOfExchange, insuranceCurrency, insuranceRateOfExchange, otherChargesCurrency, otherChargesRateOfExchange) => {
    const workSheetObject = {
        reference_number: referenceNumber,
        country: country,
        declarant: {
            name: declarantName,
            tin: declarantTIN
        },
        regime: regimeType,
        waybill: {
            importer: {
                name: importerName,
                tin: importerTIN
            },
            exporter: {
                name: exporterName,
                tin: exporterTIN
            },
            vessel: vesselName,
            port_of_discharge: portOfDischarger,
            charges: {
                external_freight: {
                    amount: parseFloat(verifyNumbers(freightAmount)),
                    currency: freightCurrency,
                    rate_of_exchange: freightRateOfExchange
                },
                insurance: {
                    amount: parseFloat(verifyNumbers(insurance)),
                    currency: insuranceCurrency,
                    rate_of_exchange: insuranceRateOfExchange
                },
                other_charges: {
                    amount: parseFloat(verifyNumbers(otherCharges)),
                    currency: otherChargesCurrency,
                    rate_of_exchange: otherChargesRateOfExchange
                }
            },
            // external_freight: parseFloat(verifyNumbers(freightAmount)),
            // insurance: parseFloat(verifyNumbers(insurance)),
            // other_charges: parseFloat(verifyNumbers(otherCharges))
        },
        additional_local_charges: {
            deposit: deposit,
            container_charges: containerCharges,
            additional_charges: additionalCharges,
            local_fee: localFee
        }
    }

    return workSheetObject;

}

