                    </select>
                </div>
            </div>
            <div class="col-md-3">
                <div class="form-group">
                    <label for="regimeType"><strong>Regime Type</strong></label>
                    <!-- <select class="form-select" v-model="localData.regimeType">
                        <option value="" disabled>Select a Regime Type</option>
