                    </select> -->
                    <v-select :options="localData.regimeTypeOptions" v-model="localData.regimeType" label="regimeType" :searchable="true" :reduce="regime => regime.regimeTypeId"  :filterable="true" placeholder="Select a Regime Type"></v-select>
                </div>
            </div>
            <div class="col-md-3">
