                netPrice = netPrice + this.customsEntryForm.freightAmount + this.customsEntryForm.insuranceAmount;
            }
            console.log("Invoice Numbers: ", invoiceNumbers);
            console.log("Incoterms: ", this.customsEntryForm.incoTerms);
