<template>
    <div class="declarants-page">
      <h1>Declarants List</h1>
      <form class="my-form-container" @submit.prevent="addDeclarant">
        <div class="row">
          <div class="col-md-3">
            <div class="form-group">
              <label for="declarantName"><strong>Name</strong></label>
              <input type="text" class="form-control" id="declarantName" placeholder="Please Enter Declarant Name" v-model="declarantName" />
            </div>
          </div>
          <div class="col-md-3">
            <div class="form-group">
              <label for="declarantAddressLine1"><strong>Address Line</strong></label>
              <input type="text" class="form-control" id="declarantAddressLine1" placeholder="Please Enter Address Line" v-model="declarantAddressLine1" />
            </div>
          </div>
          <div class="col-md-3">
            <div class="form-group">
              <label for="declarantAddressLine2"><strong>Address Line 2</strong></label>
              <input type="text" class="form-control" id="declarantAddressLine2" placeholder="Please Enter Address Line 2" v-model="declarantAddressLine2" />
            </div>
          </div>
          <div class="col-md-3">
            <div class="form-group">
              <label for="declarantCity"><strong>City</strong></label>
              <input type="text" class="form-control" id="declarantCity" placeholder="Please Enter City" v-model="declarantCity" />
            </div>
          </div>
          
         
        </div>
        <div class="row">
          <div class="col-md-3">
            <div class="form-group">
              <label for="declarantCountry"><strong>Country</strong></label>
              <!-- <input type="text" class="form-control" id="declarantCountry" v-model="declarantCountry" /> -->
              <!-- <select class="form-select" v-model="declarantCountry" id="declarantCountry">
                <option disabled value="">Please select one</option>
                <option v-for="country in this.countries" :key="country.code" :value="country.name">
                  {{ country.name }}
                </option>
              </select> -->
              <v-select
                v-model="declarantCountry"
                :options="countries"
                label="name"
                placeholder="Select Country"
                :searchable="true"
                :reduce="country => country.name"
                :filterable="true"
                ></v-select>
            </div>
          </div>
          <div class="col-md-3">
            <div class="form-group">
              <label for="declarantStatus"><strong>Status</strong></label>
              <input type="text" class="form-control" id="declarantStatus" placeholder="Please Enter Status" v-model="declarantStatus" />
            </div>
          </div>
          <div class="col-md-3">
            <div class="form-group">
              <label for="declarantImporter"><strong>Importer</strong></label>
              <v-select
                v-model="declarantImporter"
                :options="importers"
                label="name"
                placeholder="Select Importer"
                :searchable="true"
                :reduce="importer => importer.id"
                :filterable="true"
                ></v-select>
            </div>
          </div>
          <div class="col-md-3">
            <div class="form-group">
              <p><strong></strong></p>
              <button class="btn btn-primary" type="submit">Add New Declarant</button>
            </div>
          </div>
        </div>
      </form>
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Name</th>
            <th scope="col">Address </th>
            <th scope="col">Country</th>
            <th scope="col">Status</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(declarant, index) in declarants" :key="index">
              <th scope="row">{{ index + 1 }}</th>
              <td>{{ declarant.name }}</td>
              <td>{{ declarant.address }}</td>
              <td>{{ declarant.country }}</td>
              <td>{{ declarant.status }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  import * as DatabaseConnector from '@/composables/DatabaseConnector';
  import * as SweetAlertConnector from '@/composables/SweetAlertConnector';

  export default {
    name: 'DeclarantsPage',
    data() {
      return {
        declarants: [],
        countries: [],
        importers: [],
        declarantName: '',
        declarantAddressLine1: '',
        declarantAddressLine2: '',
        declarantCity: '',
        declarantCountry: '',
        declarantStatus: '',
        declarantImporter: '',
      };
    },
    methods: {
      async getDeclarants() {
        this.declarants = await DatabaseConnector.fetchDeclarants();
      },
      async getContries() {
        this.countries = await DatabaseConnector.fetchCountries();
      },
      async fetchImporters(){
        this.importers = await DatabaseConnector.fetchBuyers();
      },

      async addDeclarant() {
        if (!this.declarantName || !this.declarantAddressLine1 || !this.declarantCity || !this.declarantCountry || !this.declarantStatus || !this.declarantImporter) {
          SweetAlertConnector.showErrorAlert('All fields are required');
          return;
        }
        const newDeclarant = {
          name: this.declarantName,
          address: `${this.declarantAddressLine1}\n${this.declarantAddressLine2}`,
          city: this.declarantCity,
          country: this.declarantCountry,
          status: this.declarantStatus,
          importerId: this.declarantImporter,
        };
        SweetAlertConnector.showLoadingAlert('Adding Declarant...');
        DatabaseConnector.addDeclarant(newDeclarant).then(() => {
          this.declarantName = '';
          this.declarantAddressLine1 = '';
          this.declarantAddressLine2 = '';
          this.declarantCity = '';
          this.declarantCountry = '';
          this.declarantStatus = '';
          this.declarantImporter = '';
          SweetAlertConnector.showSuccessAlert('Declarant Added Successfully');
          this.getDeclarants();
        }).catch((error) => {
          SweetAlertConnector.showErrorAlert('Failed to Add Declarant', error);
        });
      },
    },
    mounted() {
      this.getDeclarants();
      this.getContries();
      this.fetchImporters();
    }
  }
  </script>

  
  
  <style scoped>
  .declarants-page {
    padding: 20px;
    text-align: center;
  }
  
  .customs-declaration-section-two {
    /* Your styles here */
  }
  .form-group {
    margin-bottom: 20px; /* Adds space below each form group */
  }
  .form-check {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px; /* Adds space below each form group */
  }
  .form-check label {
    margin-right: 1rem;
  }
  .my-form-container {
    text-align: left;
  }

  table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  text-align: left;
  padding: 8px;
  border-bottom: 1px solid #ccc;
}

th {
  background-color: #f4f4f8;
  color: #333;
}

button {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
  </style>
  